<script>
  import { Swipe, SwipeItem } from "svelte-swipe";
  import { pop } from "svelte-spa-router";
  import { onMount, tick } from "svelte";

  export let index = 0;
  export let images = [];

  onMount(async () => {
    await tick();
    swipe.goTo(index);
  });

  let swipe;
  let active_item = 0;
</script>

<main>
  <div class="header h-25">
    <div class="container-fluid">
      <div class="d-flex justify-content-between align-items-center h-100">
        <div class="p-1">
          <button
            class="btn btn-floating btn-lg btn-dark"
            on:click={() => pop()}><i class="fas fa-chevron-left" /></button
          >
        </div>
        <div class="p-1">
          <div class="d-flex justify-content-center p-1">
            {#each images as image, i}
              <div
                class="img d-flex align-items-center justify-content-center m-1"
                class:img-selected={active_item == i}
              >
                <img
                  src={image}
                  class="mh-100 mw-100"
                  alt=""
                  on:click={() => {
                    swipe.goTo(i);
                  }}
                />
              </div>
            {/each}
          </div>
        </div>
        <div class="p-1">
          <h5>
            <span class="badge bg-dark text-light">
              {active_item + 1}/{images.length}
            </span>
          </h5>
        </div>
      </div>
    </div>
  </div>
  <div class="swipe h-75">
    <Swipe bind:this={swipe} bind:active_item>
      {#each images as image}
        <SwipeItem>
          <div
            class="d-flex justify-content-center align-items-center p-5 h-100"
          >
            <img src={image} alt="" class="m-auto h-auto mw-100 mh-100" />
          </div>
        </SwipeItem>
      {/each}
    </Swipe>
  </div>
</main>

<style>
  .img {
    height: 40px;
    width: 40px;
    background: #333333;
    border-radius: 10px;
  }

  .img-selected {
    background: #dddddd;
  }

  .header {
    height: 150px;
    max-height: 150px;
  }
  main {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #000000 no-repeat center center fixed;
  }

  :global(.swipeable-item-inner) {
    height: 100%;
    max-height: 100%;
  }
</style>
